/* global emergence */
document.addEventListener('DOMContentLoaded', () => {
  // Redirect non-english speakers
  if (navigator.language === 'ru' || navigator.language === 'be') {
    window.location.replace('https://betab.ozdesign.us/ru/');
  } else if (navigator.language === 'uk') {
    window.location.replace('https://betab.ozdesign.us/uk/');
  }

  // Activate details animation if we have details block
  if (document.querySelector('#js-details')) {
    emergence.init({
      reset: false,
      elemCushion: 0.5,
    });
  }
});



